import ArrowsRepeat from "../icons/ArrowsRepeat";
import {obtainGoals, obtainResults, obtainTargets} from "../../containers/cbChecklist/checkListMethods";
import store from "../../store";

const reload = () => {
    let checklistId = store.getState().siteModelChecklist?.currentChecklist.id;
    obtainTargets(checklistId);
    obtainGoals(checklistId);
    obtainResults(checklistId);
}

const ReloadButton = (props) => {
    let classes = props.classes ? props.classes : "btn btn-info";
    return <button onClick={()=>reload()} className={classes}><ArrowsRepeat /></button>
}

export default ReloadButton;