import {connect} from "react-redux";
import {useEffect} from "react";
import {
    checkWindow,
    obtainChecklists,
    obtainGoals,
    obtainResults,
    obtainTargets
} from "../../containers/cbChecklist/checkListMethods";
import store from "../../store";
import {addTabGroup, addTabToGroup} from "../../actions/uiActions";
import {createTabGroupObject,createTabModel} from "../../datamodels/foundation";

const ChecklistMonitor = () => {
    useEffect(()=>{
        if(!window.SITEMODEL) {
            checkWindow();
        }
        store.dispatch(addTabGroup(createTabGroupObject("checkTabs", "tabMuted", {}, "", {}, "kimTabActive")));
        store.dispatch(addTabToGroup(createTabModel("Choose", "checkTabs", "choose", true, true, {}, "")));
        store.dispatch(addTabToGroup(createTabModel("Manage", "checkTabs", "manage", true, false, {}, "")));
        store.dispatch(addTabToGroup(createTabModel("Execute", "checkTabs", "execute", true, false, {}, "")));

        store.dispatch(addTabGroup(createTabGroupObject("manageTabs", "tabMuted", {}, "", {}, "kimTabActive")));
        store.dispatch(addTabToGroup(createTabModel("Details", "manageTabs", "details", true, true, {}, "")));
        store.dispatch(addTabToGroup(createTabModel("Targets", "manageTabs", "targets", true, false, {}, "")));
        store.dispatch(addTabToGroup(createTabModel("Goals", "manageTabs", "goals", true, false, {}, "")));

    },[]);

    let windowSetup = store.getState().siteModelChecklist?.windowSetup;

    useEffect(()=>{
        if(windowSetup) {
            obtainChecklists();
        }
    },[windowSetup])

    let checklistId = store.getState().siteModelChecklist?.currentChecklist?.id;

    useEffect(()=>{
        if(checklistId) {
            obtainTargets(checklistId);
            obtainGoals(checklistId);
            obtainResults(checklistId);
        }
    },[checklistId])

    return <div style={{display:"none"}}>Monitor</div>
}

const mapStateToProps = (state) =>{
    return {
        siteModelChecklist:state.siteModelChecklist
    };
};

export default connect(mapStateToProps,{})(ChecklistMonitor);