import Checkbox from "./Checkbox";
import store from "../../store";
import {useEffect, useState} from "react";

const TargetRow = ({target,goals,results,depth}) => {
    let [showChildren,setShowChildren] = useState(false);
    let allTargets = store.getState().siteModelChecklist?.currentChecklistTargets ? store.getState().siteModelChecklist.currentChecklistTargets : [];
    let childs = allTargets.filter((item)=>{
        return item.parentid===target.id;
    })
    let indentAmount = 10*depth;

    let showAllChildren = store.getState().siteModelChecklist.showAllChildren ? store.getState().siteModelChecklist.showAllChildren : false;

    useEffect(()=>{
       setShowChildren(showAllChildren);
    },[showAllChildren])

    return <><tr>
        <td onClick={()=>setShowChildren(!showChildren)}>{childs.length ? showChildren ? "-" : "+" : null }</td>
        <td style={{paddingLeft:indentAmount + "%"}}>{target?.testpackage}</td>
        {goals
            .filter((item)=>{
                return item.label.length
            })
            .map((item)=>{
            let displayValue = results[target?.id] && results[target?.id][item?.id] ? results[target?.id][item?.id] : 0;
            return <td><Checkbox checklistuuid={target.checklistuuid} goal={item.id} target={target.id} result={displayValue} key={item.id + target.testpackage}/></td>
        })}
    </tr>

    {childs.length && showChildren ? childs.map((item)=>{
        return <TargetRow target={item} key={item.checklistuuid + item.testpackage} goals={goals} results={results} depth={depth+1} />
    }) : null}
        </>
}

export default TargetRow;